.navbar {
   display: flex;
   justify-content: space-between;
   align-items: center;
   padding: 1rem 2rem;
   background-color: var(--color-accent);
   position: sticky;
   top: 0;
   z-index: 100;
}

.logo {
   font-size: 1.5rem;
   font-weight: bold;
}

.nav-links {
   list-style: none;
   display: flex;
   gap: 20px;
   font-size: 1.5rem;
}

.nav-links a {
   color: var(--color-text);
   font-weight: 500;
   text-decoration: none;
   font-family: "Chakra Petch", sans-serif;
}

.nav-links a:hover {
   color: #1f4a3d; /* even darker shade for stronger contrast */
   text-shadow: 0px 2px 4px rgba(0, 0, 0, 0.3);
   transform: scale(1.05);
   transition: all 0.2s ease-in-out;
}

/* Mobile */
.hamburger {
   display: none;
   flex-direction: column;
   gap: 5px;
   cursor: pointer;
}

.hamburger div {
   width: 25px;
   height: 3px;
   background: var(--color-text);
}

@media (max-width: 768px) {
   .nav-links {
      display: none;
      position: absolute;
      top: 60px;
      right: 20px;
      flex-direction: column;
      background: var(--color-bg);
      padding: 10px;
      box-shadow: 0 2px 8px rgba(0, 0, 0, 0.2);
   }
   .nav-links.show {
      display: flex;
   }
   .hamburger {
      display: flex;
   }
}
