.hero {
   height: 100vh;
   display: flex;
   flex-direction: row;
   justify-content: space-between;
   align-items: center;
   text-align: left;
   padding: 0 45px;
   opacity: 0;
   transform: translateX(-50px);
   transition: all 1s ease;
}

.hero.fade-in {
   opacity: 1;
   transform: translateX(0);
   transition: opacity 1s ease, transform 1s ease;
}

.hero h1 {
   font-size: 3rem;
   color: var(--color-heading);
   font-weight: 700;
}

.hero p {
   margin-top: 15px;
   font-size: 1.2rem;
   color: #333333;
   font-weight: 500;
}

.hero-content {
   flex: 1;
   max-width: 50%;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   gap: 20px;
   padding-top: 30px;
   padding-bottom: 30px;
}

.hero-image {
   flex: 1;
   display: flex;
   justify-content: center;
}

.hero-image img {
   width: 50rem;
   max-width: 100%;
   height: auto;
   border-radius: 10px;
}

.btn {
   display: inline-block;
   padding: 12px 24px;
   background: linear-gradient(90deg, var(--color-link), var(--color-accent));
   color: white;
   text-decoration: none;
   border-radius: 6px;
   font-weight: 600;
   transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.btn:hover {
   background: linear-gradient(90deg, var(--color-accent), var(--color-link));
   transform: scale(1.05);
   box-shadow: 0px 4px 15px rgba(0, 0, 0, 0.3);
}
